import { AppSidebar } from "@/components/app-sidebar"
import {
  Breadcrumb,
  BreadcrumbItem,
  BreadcrumbLink,
  BreadcrumbList,
  BreadcrumbPage,
  BreadcrumbSeparator,
} from "@/components/ui/breadcrumb"
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/components/ui/card"
import { Separator } from "@/components/ui/separator"
import {
  SidebarInset,
  SidebarProvider,
  SidebarTrigger,
} from "@/components/ui/sidebar"
import {
  Settings,
  Users,
  Shield,
  Database,
  Mail,
  Globe,
  Key,
  FileText,
  Zap,
  Clock,
  Archive,
  Bell,
  Palette,
  Download,
  Upload,
  Monitor,
} from "lucide-react"

export default function Page() {
  const adminCards = [
    {
      title: "Benutzer & Rollen",
      description: "Benutzerverwaltung und Berechtigungen",
      icon: Users,
      href: "/admin/users",
    },
    {
      title: "Systemeinstellungen",
      description: "Allgemeine Systemkonfiguration",
      icon: Settings,
      href: "/admin/settings",
    },
    {
      title: "Sicherheit",
      description: "Sicherheitsrichtlinien und Zugriffskontrollen",
      icon: Shield,
      href: "/admin/security",
    },
    {
      title: "Datenbank",
      description: "Datenbankwartung und Backup-Einstellungen",
      icon: Database,
      href: "/admin/database",
    },
    {
      title: "E-Mail Konfiguration",
      description: "SMTP-Einstellungen und E-Mail-Vorlagen",
      icon: Mail,
      href: "/admin/email",
    },
    {
      title: "Netzwerk & Domains",
      description: "Netzwerkeinstellungen und Domain-Konfiguration",
      icon: Globe,
      href: "/admin/network",
    },
    {
      title: "API & Schlüssel",
      description: "API-Konfiguration und Authentifizierung",
      icon: Key,
      href: "/admin/api",
    },
    {
      title: "Berichte & Logs",
      description: "Systemprotokollierung und Berichterstattung",
      icon: FileText,
      href: "/admin/reports",
    },
    {
      title: "Integrationen",
      description: "Externe System-Integrationen",
      icon: Zap,
      href: "/admin/integrations",
    },
    {
      title: "Zeitplanung",
      description: "Automatisierte Tasks und Cron-Jobs",
      icon: Clock,
      href: "/admin/scheduling",
    },
    {
      title: "Archivierung",
      description: "Datenarchivierung und Aufbewahrung",
      icon: Archive,
      href: "/admin/archiving",
    },
    {
      title: "Benachrichtigungen",
      description: "System-Benachrichtigungen und Alerts",
      icon: Bell,
      href: "/admin/notifications",
    },
    {
      title: "Design & Themes",
      description: "UI-Anpassungen und Corporate Design",
      icon: Palette,
      href: "/admin/themes",
    },
    {
      title: "Import/Export",
      description: "Datenimport und -export Funktionen",
      icon: Download,
      href: "/admin/import-export",
    },
    {
      title: "Backup & Restore",
      description: "System-Backup und Wiederherstellung",
      icon: Upload,
      href: "/admin/backup",
    },
    {
      title: "Monitoring",
      description: "Systemüberwachung und Performance",
      icon: Monitor,
      href: "/admin/monitoring",
    },
  ]

  return (
    <SidebarProvider>
      <AppSidebar />
      <SidebarInset>
        <header className="flex h-16 shrink-0 items-center gap-2">
          <div className="flex items-center gap-2 px-4">
            <SidebarTrigger className="-ml-1" />
            <Separator
              orientation="vertical"
              className="mr-2 data-[orientation=vertical]:h-4"
            />
            <Breadcrumb>
              <BreadcrumbList>
                <BreadcrumbItem>
                  <BreadcrumbPage>Administration</BreadcrumbPage>
                </BreadcrumbItem>
              </BreadcrumbList>
            </Breadcrumb>
          </div>
        </header>
        <div className="flex flex-1 flex-col gap-4 p-4 pt-0">
          <div className="flex items-center justify-between mb-4">
            <h1 className="text-3xl font-semibold">Administration</h1>
          </div>

          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
            {adminCards.map((card, index) => (
              <Card
                key={index}
                className="hover:shadow-md transition-shadow cursor-pointer"
                onClick={() => (window.location.href = card.href)}
              >
                <CardContent className="flex flex-col items-center justify-center p-6 text-center">
                  <card.icon className="h-12 w-12 text-muted-foreground mb-4" />
                  <CardTitle className="text-sm font-medium mb-2">
                    {card.title}
                  </CardTitle>
                  <CardDescription className="text-xs">
                    {card.description}
                  </CardDescription>
                </CardContent>
              </Card>
            ))}
          </div>
        </div>
      </SidebarInset>
    </SidebarProvider>
  )
}
